var PIXI         = require("../node_modules/pixi.js/dist/pixi.min");
var app          = new PIXI.Application(2800, 2600, { backgroundColor: 0x1099bb}),
    Container    = PIXI.Container,
    Sprite       = PIXI.Sprite,
    Loader       = PIXI.loader,
    Texture      = PIXI.Texture,
    AnimatedSprite = PIXI.extras.AnimatedSprite,
    TilingSprite = PIXI.extras.TilingSprite;

var stage;


Loader
    .add('img/sea.png')
    .add('img/ship0.json')
    .add('img/ship1.json')
    .load(init);

function init(){
    stage = new Container();

    document.body.appendChild(app.view);
    app.stage.addChild(stage);
    
    var bg = new TilingSprite(
        Loader.resources["img/sea.png"].texture, 
        app.renderer.width,
        app.renderer.height
    );

    stage.addChild(bg);
    var i;
    var frames = [];
    for (i = 1; i < 15; i++) {
        frames.push(Texture.fromFrame(i + '.png'));
    }

    var frames2 = [];
    var index = '1000';
    for (i = 1; i < 14; i++) {
        if(i > 9){
            index = '100';
        }
        frames2.push(Texture.fromFrame('Ship_' + index + i + '.png'));
    }

    var anim = new AnimatedSprite(frames);
    anim.animationSpeed = 0.25;
    anim.anchor.set(0.5);
    anim.x = 200;
    anim.y = 200;
    // anim.play();

    app.stage.addChild(anim);

    // var anim2 = new AnimatedSprite(frames2);
    // anim2.x = 400;
    // anim2.y = 400;
    
    // anim2.animationSpeed = 0.25;
    // anim2.play();

    // anim2.anchor.set(0.5);
    // app.ticker.add(() => {
    //     anim.rotation += 0.03;
    //     anim2.rotation += 0.01;
    // });
    // app.stage.addChild(anim2);

    document.addEventListener('keydown', (e) => {
        if(e.keyCode === 37){ // left
            anim.x -= 10;
            anim.play();
        }else if(e.keyCode === 38){ // up
            anim.y -= 10;
            anim.play();
        }else if(e.keyCode === 39){ // right
            anim.x += 10;
            anim.play();
        }else if(e.keyCode === 40){ // down
            anim.y += 10;
            anim.play();
        }
    }); 

    document.addEventListener('keyup', (e) => {
        anim.stop();
        if(e.keyCode === 37){ // left
        }else if(e.keyCode === 38){ // up

        }else if(e.keyCode === 39){ // right

        }else if(e.keyCode === 40){ // down

        }
    }); 
    
}